import React, { Component } from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./home";
import Bubble from "./bubbleSort/sort";
import Selection from "./selectionSort/sort";
import Insertion from "./insertionSort/sort";
import Quick from "./quickSort/sort";
import Comparision from "./comparision/sort";

class Sort extends Component {
  render() {
    return (
      <Router basename="/sort">
        <Switch>
          <Route path="/bubbleSort" component={Bubble} />
          <Route path="/selectionSort" component={Selection} />
          <Route path="/insertionSort" component={Insertion} />
          <Route path="/quickSort" component={Quick} />
          <Route path="/comparision" component={Comparision} />
          <Route path="/" component={Home} />
        </Switch>
      </Router>
    );
  }
}

export default Sort;
