import React, {Component} from 'react';
import DiscreteSlider from "./slider";

class Menu1 extends Component {
    render() {
        return (
            <div id="menu1">
            <div className="d-flex justify-content-between">
                <div id="indication">
                    <div className="d-flex flex-row">
                    <div className="p-2"><div className="indicationBox" id="box1"></div><h5>Nil</h5></div> 
                    <div className="p-2"><div className="indicationBox" id="box2"></div><h5>Sorting</h5></div>
                    <div className="p-2"><div className="indicationBox" id="box3"></div><h5>Sorted</h5></div>
                    <div className="p-2"><div className="indicationBox" id="box4"></div><h5>Left Part</h5></div>
                    <div className="p-2"><div className="indicationBox" id="box5"></div><h5>Right Part</h5></div>                    
                    </div>
                </div>  
                <div></div>
                <div></div>
                <div></div>
                <div id="sizeSlider">
                <DiscreteSlider
                    default={25}
                    min={5}
                    max={50}
                    step={5}
                    title="Size"
                    onCountChange={this.props.onCountChange}
                    disable={this.props.disable}
                />
                </div>
                <div></div>              
            </div>
            
            </div>
        );
    }
    isClickable = () =>{
        if( this.props.disable ){
            return {cursor: "not-allowed"};
        } else{
            return {};
        }
    }
}


export default Menu1;