import React, {Component} from 'react';
import './style.css';
class Rect extends Component {

    render() {
        return (
            <div>
                <div
                    className='rect'
                    style={{height:this.props.rect.width,
                        background:this.checkColor(),
                        margin:this.props.marg,
                        borderTopLeftRadius:5,
                        borderTopRightRadius:5,
                        borderBottomLeftRadius:0,
                        borderBottomRightRadius:0

                    }}
                ></div>
                <div
                    style={{
                        width:this.props.rect.height,
                        height: "4px",
                        background:"white",
                        marginBottom:10
                    }}
                ></div>
                <div><button className="changeButton" >+</button></div>
                <div><button className="changeButton" >-</button></div>
            </div>
        );
    }

    checkColor = () => {
        if( this.props.rect.isSorted ){
            return "#77d65c";
        } else if( this.props.rect.isSorting ){
            return "#d9200f";
        } else{
            return "white"
        }
    }
}

export default Rect;